import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
// import { ReactQueryDevtools } from '@tanstack/react-query-devtools';
import { ToastContainer } from 'react-toastify';
import { ThemeProvider } from 'styled-components';
import { GlobalStyles, theme } from './styles/theme';
import queryClient from './hooks/queryClient';
// Components - ì‹¤ì œ ë””ë ‰í† ë¦¬ êµ¬ì¡°ì— ë§ê²Œ ìˆ˜ì •
import Layout from './components/common/Layout';
import DashboardPage from './components/dashboard/DashboardPage';  // pagesê°€ ì•„ë‹Œ componentsì— ìˆìŒ
import InventoryPage from './components/inventory/InventoryPage';
import ReceiptPage from './components/receipt/ReceiptPage';
import PurchaseRequestPage from './components/purshase/PurchaseRequestPage';  // purshase (ì˜¤íƒ€) í´ë”ëª… ê·¸ëŒ€ë¡œ
// import KakaoPage from './components/kakao/KakaoPage';
import UploadPage from './components/upload/UploadPage';
import StatisticsPage from './components/statistics/StatisticsPage';
import LogsPage from './components/logs/LogsPage';

// Styles
import 'react-toastify/dist/ReactToastify.css';



const App: React.FC = () => {
  return (
    <QueryClientProvider client={queryClient}>
      <ThemeProvider theme={theme}>
        <GlobalStyles />
        <Router>
          <div className="App">
            <Routes>
              <Route path="/" element={<Layout />}>
                <Route index element={<Navigate to="/dashboard" replace />} />
                <Route path="dashboard" element={<DashboardPage />} />
                <Route path="inventory" element={<InventoryPage />} />
                <Route path="receipts" element={<ReceiptPage />} />
                <Route path="purchase-requests" element={<PurchaseRequestPage />} />
                {/* <Route path="kakao" element={<KakaoPage />} /> */}
                <Route path="upload" element={<UploadPage />} />
                {/* <Route path="statistics" element={<StatisticsPage />} /> */}
                {/* <Route path="logs" element={<LogsPage />} /> */}
                <Route path="*" element={<Navigate to="/dashboard" replace />} />
              </Route>
            </Routes>
            
            {/* Toast ì•Œë¦¼ */}
            <ToastContainer
              position="top-right"
              autoClose={3000}
              hideProgressBar={false}
              newestOnTop={false}
              closeOnClick
              rtl={false}
              pauseOnFocusLoss
              draggable
              pauseOnHover
              theme="light"
            />
          </div>
        </Router>
        
        {/* React Query DevToolsëŠ” í”„ë¡œë•ì…˜ì—ì„œ ì œê±° */}
        {/* 
        {import.meta.env.DEV && (
          <ReactQueryDevtools initialIsOpen={false} />
        )}
        */}
      </ThemeProvider>
    </QueryClientProvider>
  );
};

export default App;